import * as $axios from 'axios'

export default {
  ac_receivedMsgIntervalSet ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/mail/interval',
      params: param
    }).then((res) => {
      if (res.status === 200) {
        commit('mut_originEmailSetStatus', true)
      }
      return res.data
    }).then((d) => {
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_originEmailSet ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/mail/settings',
      data: param
    }).then(res => {
      if (res.status === 200) {
        commit('mut_originEmailSetStatus', true)
      }
      return res.data
    }).then(d => {
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_EmailSetShow ({commit}, param) {
    $axios({
      method: 'get',
      url: '/oa/mail/settings/show',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      commit('mut_mailDetail', d)
      commit('mut_receivedMsgSetStatus', false)
      commit('mut_originEmailSetStatus', false)
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  }
}



