export default {
  mut_posList (st, data) {
    st.posList = data
  },
  mut_totalElements (st, data) {
    st.totalElements = data
  },
  mut_totalPages (st, data) {
    st.totalPages = data
  },
  mut_addPosStatus (st, data) {
    st.addPosStatus = data
  },
  mut_addPosMsg (st, data) {
    st.addPosMsg = data
  },
  mut_delPosStatus (st, data) {
    st.delPosStatus = data
  },
  mut_delPosMsg (st, data) {
    st.delPosMsg = data
  }
}



