import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
// const test = resolve => require.ensure([], () => resolve(require('../pages/test.vue')), 'test')

const signin = resolve => require.ensure([], () => resolve(require('../pages/sign/signin.vue')), 'sign')

const pgIndex = resolve => require.ensure([], () => resolve(require('../pages/index.vue')), 'index')

// 人事管理
const department = resolve => require.ensure([], () => resolve(require('../pages/personnel/department/department.vue')), 'department')
const position = resolve => require.ensure([], () => resolve(require('../pages/personnel/position/position.vue')), 'position')
const employee = resolve => require.ensure([], () => resolve(require('../pages/personnel/employee/employee.vue')), 'employee')

// 邮件过滤管理
const set = resolve => require.ensure([], () => resolve(require('../pages/email/set/emailSet.vue')), 'set')
const screenResume = resolve => require.ensure([], () => resolve(require('../pages/email/screenResume/screenResume.vue')), 'screenResume')
const keywords = resolve => require.ensure([], () => resolve(require('../pages/email/keywords/keywords.vue')), 'keywords')
const processed = resolve => require.ensure([], () => resolve(require('../pages/email/processed/processed.vue')), 'processed')

// 部门招聘管理
const mailFilter = resolve => require.ensure([], () => resolve(require('../pages/recruiment/mailFilter/mailFilter.vue')), 'mailFilter')
const mailTodo = resolve => require.ensure([], () => resolve(require('../pages/recruiment/mailTodo/mailTodo.vue')), 'mailTodo')
const initialTest = resolve => require.ensure([], () => resolve(require('../pages/recruiment/initialTest/initialTest.vue')), 'initialTest')
const reExam = resolve => require.ensure([], () => resolve(require('../pages/recruiment/reExam/reExam.vue')), 'reExam')
const entry = resolve => require.ensure([], () => resolve(require('../pages/recruiment/entry/entry.vue')), 'entry')

// 消息
const recruMsg = resolve => require.ensure([], () => resolve(require('../pages/message/recruMsg/recruMsg.vue')), 'recruMsg')
const sysMsg = resolve => require.ensure([], () => resolve(require('../pages/message/sysMsg/sysMsg.vue')), 'sysMsg')

// 可靠性保障
const forwardWarn = resolve => require.ensure([], () => resolve(require('../pages/reliability/warn/forwardWarn.vue')), 'forwardWarn')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'signRoot',
      component: signin
    },
    {
      path: '/signin',
      name: 'signin',
      component: signin
    },
    {
      path: '/o',
      component: pgIndex,
      children: [
        {
          path: '1-1',
          name: 'department',
          component: department
        }, {
          path: '1-2',
          name: 'position',
          component: position
        }, {
          path: '1-3',
          name: 'employee',
          component: employee
        }, {
          path: '2-1',
          name: 'set',
          component: set
        }, {
          path: '2-2',
          name: 'screenResume',
          component: screenResume
        }, {
          path: '2-3',
          name: 'keywords',
          component: keywords
        }, {
          path: '2-4',
          name: 'processed',
          component: processed
        }, {
          path: '3-1',
          name: 'mailFilter',
          component: mailFilter
        }, {
          path: '3-2',
          name: 'mailTodo',
          component: mailTodo
        }, {
          path: '3-3',
          name: 'initialTest',
          component: initialTest
        }, {
          path: '3-4',
          name: 'reExam',
          component: reExam
        }, {
          path: '3-5',
          name: 'entry',
          component: entry
        }, {
          path: '4-1',
          name: 'recruMsg',
          component: recruMsg
        }, {
          path: '4-2',
          name: 'sysMsg',
          component: sysMsg
        }, {
          path: '5-1',
          name: 'forwardWarn',
          component: forwardWarn
        },
      ]
    }
  ]
})



