import * as $axios from 'axios'

export default {
  ac_msgList ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/jms/list',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      commit('mut_msgList', d.content)
      commit('mut_msgListTotal', d.totalElements)
      commit('mut_feedbackStatus', false)
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_msgFeedback ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/jms/feedback',
      data: param
    }).then(res => {
      commit('mut_feedbackStatus', true)
      return res.data
    }).then(d => {
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_msgReceive ({commit}) {
    $axios({
      method: 'get',
      url: '/oa/jms/receive',
    }).then(res => {
      // commit('mut_feedbackStatus', true)
      console.log('新消息', res)
      return res.data
    }).then(d => {
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_msgView ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/jms/' + param.msgId + '/show'
    }).then(res => {
      return res.data
    }).then(d => {
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  }
}



