import getters from './getters'
import actions from './actions'
import mutations from './mutations'

export default {
  namespaced: true,
  state: {
    keywordsList: [],
    keywordsListTotal: 0,
    addKeywordsStatus: {
      status: false,
      statusMsg: ''
    },
    delKeywordsStatus: {
      status: false,
      statusMsg: ''
    },
    importStatus: {
      status: 0, // 1 成功， 2 失败错误， 0 关闭提示
      msg: ''
    },
  },
  getters,
  actions,
  mutations,
}



