export default {
  mut_departmentList (st, data) {
    st.departmentList = data
  },
  mut_isDepartmentList (st, data) {
    st.isDepartmentList = data
  },
  mut_depStatusMsg (st, data) {
    st.depStatusMsg = data
  },
  mut_delDepartmentList (st, data) {
    st.delDepartmentList = data
  },
  mut_delDepStatusMsg (st, data) {
    st.delDepStatusMsg = data
  },
}



