import * as $axios from 'axios'

export default {
  ac_screenResumeList ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/resume/list/shield',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      commit('mut_screenResumeList', d.content)
      commit('mut_screenResumeListTotal', d.totalElements)
      commit('mut_delStatus', false)
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_delResume ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/resume/' + param.resumeId + '/delete'
    }).then(res => {
      return res.data
    }).then(d => {
      if (d.status === 'success') {
        commit('mut_delStatus', true)
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_resumeList ({commit}, param) {
    console.log('简历列表参数', param) // todo 简历列表参数
    $axios({
      method: 'post',
      url: '/oa/resume/list',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      commit('mut_resumeList', d.content)
      commit('mut_resumeListTotal', d.totalElements)
      commit('mut_operationRecordAddStatus', {
        status: false,
        msg: ''
      })
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_resumeHistoryList ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/resume/screen/list',
      data: param,
    }).then((res) => {
      return res.data
    }).then(d => {
      commit('mut_resumeHistoryList', d)
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_OperationRecordAdd ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/resume/screen/add',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      if (d.status === 'success') {
        commit('mut_operationRecordAddStatus', {
          status: true,
          msg: '操作成功'
        })
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  }
}



