export default {
  mut_keywordsList (st, data) {
    st.keywordsList = data
  },
  mut_keywordsListTotal (st, data) {
    st.keywordsListTotal = data
  },
  mut_addKeywordsStatus (st, data) {
    st.addKeywordsStatus = data
  },
  mut_delKeywordsStatus (st, data) {
    st.delKeywordsStatus = data
  },
  mut_importStatus (st, data) {
    st.importStatus = data
  }
}



