import * as $axios from 'axios'
import { roles } from '../utils/baseConfig'

export default {
  ac_login ({commit}, param) {
    commit('mut_loginStatus', {
      status: false,
      msg: ''
    })
    $axios({
      method: 'post',
      url: '/oa/user/login',
      data: param
    }).then((res) => {
      return res.data
    }).then((d) => {
      // console.log(d)
      if (d.status === 'success') {
        commit('mut_loginStatus', {
          status: true,
          msg: '登陆成功'
        })
        commit('mut_user', d.entity)
        sessionStorage.user = JSON.stringify(d.entity)
        // 判断是否是管理员
        let isAdmin = d.entity.roles.some(function (role) { // todo 测试普通人员
          return role.id === roles.admin
        })
        if (!isAdmin) {
          commit('mut_departmentId', d.entity.job.department.id)
        }

        commit('mut_userLoginStatus', false) // 初试接口未登录判断
      } else if (d.statusCode === '1004') {
        commit('mut_loginStatus', {
          status: false,
          msg: '用户名或密码错误'
        })
        sessionStorage.user = ''
      }
    })
  },
  ac_logout ({commit}) {
    $axios.post('/oa/user/logout').then((res) => {
      return res.data
    }).then((d) => {
      if (d.status === 'success') {
        sessionStorage.user = ''
        commit('mut_user', '')
        commit('mut_departmentId', '')
      }
    })
  },
  ac_userDetail ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/user/' + param.userId + '/show',
    }).then((res) => {
      return res.data
    }).then((d) => {
      console.log(d)
      // commit('mut_user', '')
      if (d.statusCode === '1001') {
        commit('mut_userLoginStatus', true)
      }
    })
  },
  ac_userAddAndModify ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/user/add',
      data: param
    }).then((res) => {
      return res.data
    }).then((d) => {
      if (d.status === 'success') {
        commit('mut_addUserStatus', true)
      }
      if (d.statusCode === '1001') {
        commit('mut_userLoginStatus', true)
      }
    })
  },
  ac_userDel ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/user/' + param.userId + '/delete',
    }).then((res) => {
      return res.data
    }).then((d) => {
      if (d.status === 'success') {
        commit('mut_delUserStatus', true)
      }
      if (d.statusCode === '1001') {
        commit('mut_userLoginStatus', true)
      }
    })
  },
  ac_userList ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/user/list',
      data: param
    }).then((res) => {
      return res.data
    }).then((d) => {
      commit('mut_userList', d.content)
      commit('mut_totalPagesEmp', d.totalPages)
      commit('mut_totalElementsEmp', d.totalElements)
      commit('mut_delUserStatus', false)
      commit('mut_addUserStatus', false)
      if (d.statusCode === '1001') {
        commit('mut_userLoginStatus', true)
      }
    })
  }
}



