import * as $axios from 'axios'

export default {
  ac_posList ({commit}, param) { // 职位列表
    $axios({
      method: 'post',
      url: '/oa/job/list',
      data: param
    }).then((res) => {
      return res.data
    }).then((d) => {
      commit('mut_posList', d.content)
      commit('mut_totalElements', d.totalElements)
      commit('mut_totalPages', d.totalPages)
      commit('mut_addPosStatus', false)
      commit('mut_delPosStatus', false)
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_posAddAndModify ({commit}, param) { // 职位添加或修改
    commit('mut_addPosMsg', '')
    $axios({
      method: 'post',
      url: '/oa/job/add',
      data: param
    }).then((res) => {
      return res.data
    }).then((d) => {
      if (d.status === 'success') {
        commit('mut_addPosStatus', true) // 列表置false
        commit('mut_addPosMsg', '操作成功！')
      } else {
        commit('mut_addPosMsg', '错误！')
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_posDelete ({commit}, param) { // 职位删除
    $axios({
      method: 'post',
      url: '/oa/job/' + param.jobId + '/delete',
    }).then((res) => {
      return res.data
    }).then((d) => {
      if (d.status === 'success') {
        commit('mut_delPosStatus', true)
        commit('mut_delPosMsg', '删除成功')
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  }
}



