import * as $axios from 'axios'

export default {
  ac_keywordsList ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/resume/key/list',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      commit('mut_keywordsList', d.content)
      commit('mut_keywordsListTotal', d.totalElements)
      commit('mut_addKeywordsStatus', {
        status: false,
        statusMsg: ''
      })
      commit('mut_delKeywordsStatus', {
        status: false,
        statusMsg: ''
      })
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_addAndModifyKeywords ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/resume/key/add',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      if (d.status === 'success') {
        commit('mut_addKeywordsStatus', {
          status: true,
          statusMsg: '操作成功'
        })
      } else if (d.statusCode === '3001') {
        commit('mut_addKeywordsStatus', {
          status: false,
          statusMsg: '关键字存在'
        })
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_delKeywords ({commit}, param) {
    $axios({
      method: 'post',
      url: '/oa/resume/key/' + param.resumeKeyId + '/delete',
    }).then(res => {
      return res.data
    }).then(d => {
      if (d.status === 'success') {
        commit('mut_delKeywordsStatus', {
          status: true,
          statusMsg: '删除成功'
        })
      } else {
        commit('mut_delKeywordsStatus', {
          status: false,
          statusMsg: '删除失败'
        })
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_keywordsExport ({commit}, param) {
    $axios({
      method: 'get',
      url: '/oa/resume/key/export',
      // data: param
    }).then(res => {
      return res.data
    }).then(d => {
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_keywordsImport ({commit}, param) {
    commit('mut_importStatus', {
      status: 0,
      msg: ''
    })
    $axios({
      method: 'post',
      url: '/oa/resume/key/import',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      if (d.status === 'success') {
        commit('mut_importStatus', {
          status: 1,
          msg: '导入成功'
        })
      } else if (d.statusCode === '4001') {
        commit('mut_importStatus', {
          status: 2,
          msg: '表格数据错误'
        })
      } else if (d.statusCode === '4002') {
        commit('mut_importStatus', {
          status: 2,
          msg: '不是xls文件错误'
        })
      } else if (d.statusCode === '5001') {
        commit('mut_importStatus', {
          status: 2,
          msg: '数据保存错误'
        })
      } else if (d.statusCode === '5002') {
        commit('mut_importStatus', {
          status: 2,
          msg: '数据验证错误'
        })
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  }
}



