export default {
  mut_user (st, data) {
    st.user = data
  },
  mut_loginStatus (st, data) {
    st.loginStatus = data
  },
  mut_userDetail (st, data) {
    st.userDetail = data
  },
  mut_userList (st, data) {
    st.userList = data
  },
  mut_totalPagesEmp (st, data) {
    st.totalPages = data
  },
  mut_totalElementsEmp (st, data) {
    st.totalElements = data
  },
  mut_delUserStatus (st, data) {
    st.delUserStatus = data
  },
  mut_addUserStatus (st, data) {
    st.addUserStatus = data
  },
  mut_departmentId (st, data) {
    st.departmentId = data
  },
  mut_searchValue (st, data) {
    st.searchValue = data
  },
  mut_userLoginStatus (st, data) {
    st.userLoginStatus = data
  }
}



