import * as $axios from 'axios'

export default {
  ac_departmentList ({commit}, param) { // 部门列表
    $axios({
      method: 'get',
      url: '/oa/department/all',
    }).then((res) => {
      return res.data
    }).then((d) => {
      commit('mut_departmentList', d)
      commit('mut_isDepartmentList', false)
      commit('mut_delDepartmentList', false)
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_departmentDel ({commit}, param) { // 删除部门
    commit('mut_delDepStatusMsg', '')
    $axios({
      method: 'post',
      url: '/oa/department/' + param.departmentId + '/delete',
    }).then((res) => {
      return res.data
    }).then(d => {
      if (d.status === 'success') {
        commit('mut_delDepartmentList', true) // 在获取列表后置false
      } else {
        commit('mut_delDepStatusMsg', d.statusCode)
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
  ac_departmentAddAndModify ({commit}, param) { // 部门添加(无id)，修改(有id)
    commit('mut_depStatusMsg', '')
    $axios({
      method: 'post',
      url: '/oa/department/add',
      data: param
    }).then(res => {
      return res.data
    }).then(d => {
      if (d.status === 'success') {
        commit('mut_isDepartmentList', true) // 在获取列表后置false
      } else if (d.statusCode === '3001') {
        commit('mut_depStatusMsg', '部门已经存在')
      }
      if (d.statusCode === '1001') { // 接口未登录判断
        commit('mut_userLoginStatus', true, {root: true})
      }
    })
  },
}



