export default {
  getterUserList (st) {
    const userList = st.userList
    let newList = []
    userList.forEach(
      (row) => {
        newList.push({
          headBase64: row.head,
          name: row.name,
          email: row.email,
          password: row.password,
          phone: row.phone,
          // job: {
          //   id: row.job.id,
          // },
          principal: row.roles.some((item) => { // 负责人
            return item.id === 4
          }) ? '4' : '0',
          reExam: row.roles.some((item) => {
            return item.id === 5
          }) ? '5' : '0',
          admin: row.roles.some((item) => {
            return item.id === 1
          }) ? '1' : '0'
        })
      }
    )
    return newList
  }
}



